body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #333;
  overflow-x: hidden;
}

body.fixed-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .contenedor {
    overflow-x: hidden;
  }
}

a {
  text-decoration: none;
  color: #fff;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 5/2;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 5/2;
  margin-bottom: 5/2;
}

.contenido-centrado {
  max-width: 800px;
}

.button-cont {
  z-index: 1;
}
.button-cont .button {
  margin: 1rem;
  font-size: 3rem;
  font-weight: bold;
  border: none;
  border-radius: 0.5rem;
}
.button-cont .button:hover .button_top {
  transform: translateY(-0.33em);
}
.button-cont .button:active .button_top {
  transform: translateY(0);
}

.button_top {
  display: block;
  box-sizing: border-box;
  border: 2px solid #fff;
  border-radius: 0.5rem;
  padding: 1rem 5rem;
  backdrop-filter: blur(10px) brightness(0.6);
  backdrop-filter: blur(10px) brightness(0.6);
  transform: translateY(-0.2em);
  transition: transform 0.1s ease;
  color: #fff;
}

.button-more {
  z-index: 1;
}
.button-more .button {
  margin: 1rem;
  font-size: 2rem;
  font-weight: bold;
  border: none;
  border-radius: 0.5rem;
  text-transform: uppercase;
}
.button-more .button:hover .button_top {
  transform: translateY(-0.33em);
}
.button-more .button:active .button_top {
  transform: translateY(0);
}

.overlay2 {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay2 picture {
  display: block;
  max-width: 60rem;
}

.slide {
  width: 100vw;
  height: 100vh;
  scroll-snap-align: start;
}

.wrapper {
  display: flex;
  flex-direction: row;
  width: 600vw;
  transform: rotate(90deg) translateY(-100vh);
  transform-origin: top left;
}

@media (max-width: 768px) {
  .outer-wrapper {
    transform: rotate(0) translateX(0) !important;
    transform-origin: top left !important;
    overflow-y: scroll !important;
    overflow-x: hidden !important;
    position: absolute !important;
    width: 100vw !important;
    height: 100vh !important;
  }
  .wrapper {
    display: flex !important;
    flex-direction: column !important;
    height: 600vw !important;
    transform: rotate(0) translateY(0) !important;
    transform-origin: top left !important;
  }
  .slide {
    width: 100vw !important;
    height: 100vh !important;
  }
}
.outer-wrapper {
  height: 100vw;
  transform: rotate(-90deg) translateX(-100vh);
  transform-origin: top left;
  overflow-y: scroll;
  overflow-x: hidden;
  position: absolute;
  scrollbar-width: none;
  -ms-overflow-style: none;
  scroll-behavior: smooth;
  overflow: auto;
  scroll-snap-type: y mandatory;
  scroll-snap-type: y proximity;
}
@media (max-width: 768px) {
  .outer-wrapper {
    scroll-snap-type: none;
  }
}

::-webkit-scrollbar {
  display: none;
}

.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  scroll-snap-type: x mandatory;
}

.rotate {
  background-color: #fafafa;
  transform-origin: top left;
  transition: transform 0.5s linear;
  width: 100vw;
  min-height: 100vh;
}

.rotate.show-nav {
  transform: rotate(-15deg);
}

.rotate.show-nav + nav li {
  transform: translateX(0);
  transition-delay: 0.3s;
}

.rotate.show-nav .circle {
  transform: rotate(-70deg);
}

.content h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 50px;
}

.overlay {
  background: linear-gradient(rgba(2, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.rotate {
  background-color: #fafafa;
  transform-origin: top left;
  transition: transform 0.5s linear;
  width: 100vw;
  min-height: 100vh;
}

.rotate.show-nav {
  transform: rotate(-15deg);
}

.rotate.show-nav + nav li {
  transform: translateX(0);
  transition-delay: 0.3s;
}

.rotate.show-nav .circle {
  transform: rotate(-70deg);
}

.content h2 {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 50px;
}

.container {
  background-color: #575757;
  transform-origin: top left;
  transition: transform 0.4s linear;
  min-height: 100vh;
  position: relative;
  transform: translateX(0px);
  width: 100vw !important;
}

.container.show-nav {
  transform: translate(175px);
}

.container.show-nav + nav li {
  transform: translateX(0);
}

.container.show-nav .circle {
  transform: rotate(-70deg);
}

.container.show-nav::before {
  content: "";
  background: linear-gradient(rgba(2, 0, 0, 0.5), rgba(0, 0, 0, 0.5));
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  z-index: 3;
}

.circle-container {
  position: fixed;
  top: -100px;
  left: -100px;
  z-index: 10;
}

.circle {
  backdrop-filter: blur(10px) brightness(0.4);
  height: 200px;
  width: 200px;
  border-radius: 50%;
  position: relative;
  transition: transform 0.5s linear;
}

.circle.show-nav {
  transform: rotate(-90deg);
}

.circle button {
  cursor: pointer;
  position: absolute;
  top: 50%;
  left: 50%;
  height: 100px;
  background: transparent;
  border: 0;
  font-size: 26px;
  color: #fff;
}

.circle button:focus {
  outline: none;
}

.circle button#open {
  left: 60%;
}

.circle button#close {
  top: 60%;
  transform: rotate(90deg);
  transform-origin: top left;
}

nav {
  position: fixed;
  top: 100px;
  left: 0;
  z-index: 100;
  left: -16rem;
  top: 10rem;
}

nav.active {
  left: 0rem;
}

nav ul {
  list-style: none;
  padding-left: 15px;
}

nav ul li {
  text-transform: uppercase;
  color: #fff;
  margin: 40px 0;
  transform: translateX(-150%);
  transition: transform 0.4s ease-in;
}
nav ul li a {
  padding: 2rem 2rem 2rem 0;
}

nav ul li i {
  font-size: 20px;
  margin-right: 10px;
}

.content-home {
  background: #191926;
  height: 100vh;
  display: flex;
  align-items: center;
  position: relative;
}
.content-home .before {
  background-image: url("/build/img/cmsRC6.2.1-min.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  animation: 80s ease 0s normal none infinite running zoomEffect;
  z-index: 0;
}
@keyframes zoomEffect {
  from {
    transform: scale(1, 1);
  }
  to {
    transform: scale(2, 2);
  }
}
.content-home .after {
  position: absolute;
  top: 0;
  left: 0;
  width: 50vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.1);
  backdrop-filter: brightness(0.5) blur(20px);
  z-index: 0;
}
@media (max-width: 768px) {
  .content-home .after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    backdrop-filter: brightness(0.5) blur(10px);
  }
}
.content-home .bannertext {
  z-index: 2;
  width: 100%;
  padding: 2rem;
  text-align: center;
  color: #fff;
  display: flex;
  flex-direction: column;
  gap: 5rem;
  position: relative;
  top: 25%;
}
@media (max-width: 768px) {
  .content-home .bannertext {
    width: 100vw;
    top: 10%;
  }
}
.content-home .bannertext .text-banner {
  z-index: 0;
  background-color: rgba(0, 0, 0, 0.1);
}
.content-home .bannertext .name-home {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.content-home .bannertext .name-home .image-Sheru {
  width: 40%;
}
.content-home .bannertext .name-home img {
  width: 20%;
}
.content-home .bannertext h2 {
  margin: 0;
  font-weight: 900;
  height: auto;
}

.section-home .next-home {
  position: absolute;
  bottom: 2%;
  right: 0%;
  background: rgba(0, 0, 0, 0.1);
  box-shadow: 0 25px 45px rgba(0, 0, 0, 0.1);
  border: 0.5px solid rgba(200, 200, 200, 0.5);
  border-radius: 5px;
  backdrop-filter: blur(20px);
  backdrop-filter: brightness(0.5);
  border-radius: 1rem 0 0 1rem;
  z-index: 1;
}
.section-home .next-home img {
  width: 10rem;
  margin-right: 5rem;
  filter: invert(100%);
  padding: 1rem;
}
@media (max-width: 768px) {
  .section-home .next-home {
    bottom: 0%;
    right: 2%;
    border-radius: 2rem 2rem 0 0rem;
  }
  .section-home .next-home img {
    margin-right: 0rem;
    margin-bottom: 5rem;
  }
}

